import React, { useState, useEffect, useReducer } from "react";
import {
  useAllFilmsLazyQuery,
  useAllCharacterNamesLazyQuery,
  useAllStarshipsLazyQuery,
  useAllStarshipsQuery,
  useAllCharacterNamesQuery,
  useAllFilmsQuery
} from "../generated/graphql";

const initState = {
  isLoading: true,
  mainType: "films",
  list: []
};

const reducer = (state, action) => {
  switch (action.type) {
    case "setLoading":
      return { ...state, isLoading: true };
    case "setType":
      return { ...state, mainType: action.payload };
    case "setList":
      return { ...state, list: action.payload, isLoading: false };
    default:
      return [];
  }
};

export const useTypeFilter = () => {
  const [state, dispatch] = useReducer(reducer, initState);
  const films = useAllFilmsQuery();
  const persons = useAllCharacterNamesQuery();
  const starships = useAllStarshipsQuery();

  useEffect(() => {}, [state.mainType]);

  useEffect(() => {
    if (state.mainType === "films") {
      if (films.loading) {
        dispatch({ type: "setLoading" });
      } else if (!films.error && films.data) {
        dispatch({ type: "setList", payload: films.data.allFilms });
      }
    }
  }, [films, state.mainType]);

  useEffect(() => {
    if (state.mainType === "persons") {
      if (persons.loading) {
        dispatch({ type: "setLoading" });
      } else if (!persons.error && persons.data) {
        dispatch({ type: "setList", payload: persons.data.allPersons });
      }
    }
  }, [persons, state.mainType]);

  useEffect(() => {
    if (state.mainType === "starships") {
      if (starships.loading) {
        dispatch({ type: "setLoading" });
      } else if (!starships.error && starships.data) {
        dispatch({ type: "setList", payload: starships.data.allStarships });
      }
    }
  }, [starships, state.mainType]);

  return [state, dispatch];
};

